
.Content{display:block
}
.Content .article{margin:50px 0
}
.Content .article .article-date{display:inline-block;text-decoration:none;text-transform:uppercase;letter-spacing:2px;color:#999;margin-bottom:1em;margin-left:5px;line-height:1em;text-shadow:0 1px #fff;font-weight:bold
}
.Content .article .article-inner{overflow:hidden;background-color:#fff;box-shadow:1px 2px 3px #ddd
}
.Content .article .article-inner .article-header{padding:20px 20px 0
}
.Content .article .article-inner .article-header .article-title{font-size:2em;text-decoration:none;font-weight:bold;color:#555;line-height:1.1em
}
.Content .article .article-inner .article-entry{color:#555;padding:0 20px
}
.Content .article .article-inner .article-entry p,.Content .article .article-inner .article-entry table{line-height:1.6rem;margin:1.6em 0
}
@media screen and (min-width: 768px){
.Content{float:left;width:73.3333333333%
}
}
.Content:before,.Content:after{content:"";display:table
}
.Content:after{clear:both
}



