
.sidebar{display:block
}
.sidebar .widget-wrap{margin:50px 0
}
.sidebar .widget-wrap .widget-title{text-transform:uppercase;letter-spacing:2px;color:#999;margin-bottom:1em;margin-left:5px;line-height:1em;text-shadow:0 1px #fff;font-weight:bold
}
.sidebar .widget-wrap .widget{line-height:1.6em;word-wrap:break-word;font-size:0.9em;color:#777;text-shadow:0 1px #fff;background:#ddd;box-shadow:0 -1px 4px #ccc inset;border:1px solid #ccc;padding:15px;border-radius:3px
}
.sidebar .widget-wrap .widget a{color:#258fb8;text-decoration:none
}
@media screen and (min-width: 768px){
.sidebar{float:left;width:23.333333333%;margin:0 0.83333333%
}
}



